#logoimage
{
    text-align: center;
    
    margin-left: auto; 
    position: absolute;
    top: 0px;
    display: inline-block;
    height: 50px;
    font-size: 48px;
    background: -webkit-linear-gradient(rgb(255, 93, 34), rgb(226, 2, 170));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-color: black; 
    font-family: 'Monoton', cursive;
    height: 60px;


   
    
}

#logowrapper
{
    display: inline-block;
    position: absolute;
    top: 0px;
    left: 0px;
    height: 60px;
    width: 250px    ;
    background: -webkit-linear-gradient(rgb(255, 93, 34), rgb(226, 2, 170));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-color: #000; 
    transition-duration: 200ms; 

}
#logoimage1{
    
    text-align: left;
    visibility: hidden;
    
    margin-left: 0px; 
    position: absolute;
   top: 0px;
    left: 0px;
    display: inline;
    height: 50px;
    font-size: 48px;
    background: -webkit-linear-gradient(rgb(255, 93, 34), rgb(226, 2, 170));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-color: black; 
    font-family: 'Monoton', cursive;
    height: 60px;
    

}
#logowrapper1
{ 
    display: inline-block;
    position: absolute;
    margin: 0;
    top: 0px;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    left: 0px;
    height: 60px;
    width: 250px;
    background: -webkit-linear-gradient(rgb(255, 93, 34), rgb(226, 2, 170));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-color: #000; 
  
}

@media only screen and (max-width: 1200px) {
    #logoimage {
     font-size: 36px; 
     transition-duration: 100ms; 
 
    }
    
}
@media only screen and (max-width: 1000px) {
    #logoimage {
        
        font-size: 32px; 
       

    }
   
}

    #logoimage1 {
        
      visibility: visible;
        
        transition-delay: 200ms;
    }
   
